	module.exports = {"data":{"markdownRemark":{"html":"<p>朝早い時間に、私はいくつかの夢を見ました。基本的な内容は次のとおりです：</p>\n<p>「私は魚を見つける場所に行きましたが、そこにいる人々はそれを言うことができませんでした。私は望遠鏡でそれを見て、それに魚の三輪車を見ました。 1950年代から現在までのあらゆる年齢の衣装を演じるパレードのあと、私たちはチームの前にいて、写真を撮って見つめていました。私はオフィスにオフィスがあり、テーブルがあり、古いテーブルを見て、引き出しをチェックし、未使用の紙を捨てる。右側の引き出しには、開いた後、小さな財布が付いた、この辺には、1冊か2冊の本が積み重ねられていて、ほとんどが落ちてしまいました。私は非常に小さなコンピュータタイプライターを見つけました。非常に驚いています。昼食のためにテーブルに女性の写真があります。夕食に来て、テーブルが彼のものだと思う、私は彼を気​​にしない彼は少し外国人で、彼の髪は少し、鼻は鋭く、私は隣の部屋で残りを考えて本を読んでいる。彼は知人のように一人の人と話していたが、彼は離婚していたと言って、その人はどちらが誰かと言って、大きな建物を見たのを見て、外国人学生と外国人留学生が多く、多くの知り合いが出会い、おしゃべりしています。外国人学生がドアを引き上げると、みんながよかったです。カフェテリアに行く \"</p>\n<p>この夢はどういう意味ですか？昨夜、私はテレビで、過去の衣装を見せていました。昨日の朝、友達が私にポータブルコンピュータを見せました。他のスレッドはありません。</p>","id":"/home/swain/workspace/political-life/src/pages/ja/0135.md absPath of file >>> MarkdownRemark","fileAbsolutePath":"/home/swain/workspace/political-life/src/pages/ja/0135.md","frontmatter":{"title":"8月16日 火曜日"}}},"pathContext":{"id":"/home/swain/workspace/political-life/src/pages/ja/0135.md absPath of file >>> MarkdownRemark"}}

